.product-item {
  width: calc((100% - 90px)/4);
  /*gutter=30px*3*/
  margin: 0 auto; }
  @media (max-width: 768px) {
    .product-item {
      width: calc((100% - 30px)/2); } }
  @media (max-width: 375px) {
    .product-item {
      width: 100%; } }

.product-large-item {
  width: calc((100% - 60px)/3);
  margin: 0 auto; }
  @media (max-width: 768px) {
    .product-large-item {
      width: calc((100% - 30px)/2); } }
  @media (max-width: 375px) {
    .product-large-item {
      width: 100%; } }

.store-card {
  width: calc((100% - 100px)/3);
  margin-left: 15px;
  margin-right: 15px;
  margin-bottom: 40px; }
  .store-card h4 {
    margin-top: 16px;
    margin-bottom: 8px; }
  .store-card .border {
    border-bottom: 2px darkgray solid;
    margin-bottom: 16px; }
  .store-card img {
    width: 100%;
    height: 224px; }
    @media (max-width: 768px) {
      .store-card img {
        height: 164px; } }
    @media (max-width: 375px) {
      .store-card img {
        height: 164px; } }
  .store-card button {
    background-color: #000;
    color: #ffff;
    text-align: center;
    font-size: 20px;
    width: 100%;
    padding-top: 8px;
    padding-bottom: 8px;
    margin-top: 16px; }
  @media (max-width: 768px) {
    .store-card {
      width: calc((100% - 60px)/2); } }
  @media (max-width: 375px) {
    .store-card {
      width: 100%; } }

/*部落格Card*/
.blogs-card {
  width: 100%;
  margin: 0 auto;
  margin-bottom: 40px;
  display: flex; }
  .blogs-card img {
    width: 57%;
    object-fit: cover;
    margin-right: 24px; }
    @media (max-width: 768px) {
      .blogs-card img {
        width: 54%;
        height: auto; } }
    @media (max-width: 375px) {
      .blogs-card img {
        width: 100%;
        height: 225px;
        margin-right: 0;
        margin-bottom: 16px; } }
  .blogs-card .blogs-cardBody h4 {
    color: #650300;
    font-size: 28px;
    font-weight: bold;
    margin-bottom: 8px; }
  .blogs-card .blogs-cardBody a {
    font-weight: bold;
    color: #650300; }
  .blogs-card .blogs-cardBody .blogs-more {
    display: none; }
    @media (max-width: 768px) {
      .blogs-card .blogs-cardBody .blogs-more {
        display: block; } }
    @media (max-width: 375px) {
      .blogs-card .blogs-cardBody .blogs-more {
        display: none; } }
  .blogs-card .blogs-cardBody .blogs-lg-more {
    display: flex;
    justify-content: flex-end;
    margin-top: 120px;
    font-size: 28px; }
    @media (max-width: 768px) {
      .blogs-card .blogs-cardBody .blogs-lg-more {
        display: none; } }
    @media (max-width: 375px) {
      .blogs-card .blogs-cardBody .blogs-lg-more {
        display: none; } }
  @media (max-width: 375px) {
    .blogs-card {
      display: block; } }

.pagination {
  max-width: 386px;
  /*上下頁(81px*2)+數字(42px*5)+左右邊線(14px)*/
  margin: 0 auto; }
  .pagination .pagination-list {
    display: flex; }
    .pagination .pagination-list a {
      display: block;
      padding: 12px 16px;
      color: #555;
      border: 1px solid #5555; }
      .pagination .pagination-list a:hover {
        color: #ffff;
        background-color: #000000; }
      @media (max-width: 375px) {
        .pagination .pagination-list a {
          padding: 10px 15px; } }

